.rc-table-container {
  overflow-x: auto; }

.dg-empty-state-container {
  margin: auto;
  text-align: center;
  padding-top: 100px;
  padding-bottom: 100px; }

.dg-column-header-label-active {
  color: #186ccc;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0;
  padding: 0; }

.dg-column-header-icon-container {
  float: right;
  display: flex;
  flex-direction: column;
  font-size: 7px; }

.asc .dg-column-header-icon-up {
  color: #186ccc; }

.desc .dg-column-header-icon-down {
  color: #186ccc; }

.dg-table-horizontal-scroll {
  margin: auto;
  overflow: auto; }

.dg-table-fixed-column table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0; }
  .dg-table-fixed-column table th:first-child {
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    z-index: 2; }
  .dg-table-fixed-column table td:first-child {
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    z-index: 2;
    background: #ccc; }

.table-row-error {
  background: red; }

.table-row-okay {
  background: greenyellow; }

.dg-table-header-checkbox {
  width: 20px; }

.dg-table {
  background-color: #fff;
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: auto;
  width: 100%; }

.dg-table-cell,
.dg-table-header-cell {
  position: relative; }
  .dg-table-cell:not(:first-child),
  .dg-table-header-cell:not(:first-child) {
    padding-left: 18px; }
  .dg-table-cell:not(:first-child)::before,
  .dg-table-header-cell:not(:first-child)::before {
    background-color: #fff;
    bottom: 0;
    content: '';
    left: 0;
    position: absolute;
    top: 0;
    width: 2px; }

.dg-table-header-cell {
  color: #5d6f7a;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: calc(16 / 12);
  background-color: #edf0f2;
  padding: 11px 16px;
  text-align: left; }

.dg-table-cell {
  color: #222629;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: calc(16 / 12);
  padding: 15px 16px;
  line-height: 18px; }

tr {
  border-bottom: 1px solid #ccd4d9; }
